.app-head{
    margin-bottom: 30px;
}
.app-head__title{
    margin: 0;
    font-size: 30px;
}
.app-head__info{
    font-size: 16px;
    color: #808080;
}
.app-head__info span{
    font-weight: bold;
}
.search-panel{
    width: auto;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin-right: 6px;
}
.todo-list-item{
   
}
.done{
    text-decoration: line-through;
}
.todo-list-item p{
   margin: 0;
   cursor: pointer;
}

.todo-list-item.important p{
    color: #17a2b8;
    font-weight: bold;
}

.btn-icon{
    display: block;
    width: 30px;
    height: 26px;
    border-radius: 4px;
    text-align: center;
    cursor: pointer;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.btn-icon:first-of-type{
    margin-left: auto;
    margin-right: 4px;
    border: 1px solid #28a745;
}
.btn-icon:last-of-type{
    border: 1px solid #dc3545;
}
.btn-icon:first-of-type i{
    color: #28a745;
}
.btn-icon:last-of-type i{
    color: #dc3545;
}

.btn-icon:first-of-type:hover{
    background-color: #28a745;
}
.btn-icon:last-of-type:hover{
    background-color: #dc3545;
}
.btn-icon:first-of-type:hover i{
    color: #fff;
}
.btn-icon:last-of-type:hover i{
    color: #fff;
}
.todo-list > li{
    padding: 10px ;
}
.btn{
    padding: 6px 10px;
    font-size: 14px;
}
.add-item-form{
    margin-top: 10px;
}
.btn-add{
    margin-left: 6px;
}
.todo-app{
    max-width: 400px;
    margin: 100px auto;
}

.filter-panel{
    width: auto;
    margin-bottom: 10px;
}
